<script>
  import { onMount } from "svelte";
  import Nav from "../../components/NavLightColors.svelte";
  import Footer from "../../components/Footer.svelte";
  import startVideosLazy from "../lazy";
  import parallax from "../parallax";

  function startParallax() {
    let $ = document.querySelector.bind(document);

    let blob1 = $("#blob-1");
    let blob2 = $("#blob-2");
    let blob3 = $("#blob-3");
    let blob4 = $("#blob-4");

    const xmultiplier = window.innerWidth / 960;
    const overlapPixels = xmultiplier * 30;
    const initialOffset = window.pageYOffset;

    let video1 = $("#video1").getBoundingClientRect();

    const video1YOffset = video1.top + initialOffset ;
    const video1YOffset2 = video1.top + initialOffset + 500;

    parallax(
      (lastScrollY, prefix, pos, setTickingFalse) => {
        let relativeY = lastScrollY / 4000;

        // photo1
        prefix(
          blob1.style,
          "Transform",
          "translate3d(" +
            800 * xmultiplier +
            "px," +
            pos(4750, -1640, relativeY, 0) +
            "px, 0)"
        );
        // gradient red
        prefix(
          blob2.style,
          "Transform",
          "translate3d(" +
            800 * xmultiplier +
            "px," +
            pos(204, -1400, relativeY, 0) +
            "px, 0)"
        );

        // green
        prefix(
          blob3.style,
          "Transform",
          "translate3d(" +
            868 * xmultiplier +
            "px," +
            pos(video1YOffset2, -500, relativeY, 0) +
            "px, 0)"
        );

        // red
        prefix(
          blob4.style,
          "Transform",
          "translate3d(" +
            -68 * xmultiplier +
            "px," +
            pos(video1YOffset, -500, relativeY, 0) +
            "px, 0)"
        );
        setTickingFalse();
      },
      function() {
        blob1.classList.add("force-show-sm");
        blob2.classList.add("force-show");
        blob3.classList.add("force-show");
        blob4.classList.add("force-show");
      },
      startParallax
    );
  }

  onMount(async () => {
    startParallax();
    startVideosLazy();
  });
</script>

